import React from 'react';
import { observer } from 'mobx-react';
import { Container, Button, GridList, GridListTile, GridListTileBar,  } from '@material-ui/core';
import IconButton from '@material-ui/core/IconButton';
import InfoIcon from '@material-ui/icons/Info';


// import styles from './menu.module.css'

import ProjectStore from '../../stores/ProjectStore';

import {withRouter} from 'react-router';

// import MainImage from '../../data/images/Title.png';

@observer
class Projects extends React.Component {

    constructor(props){
        super(props);
    }

    render(){
        return(
            <Container>
                <GridList>
                    {ProjectStore.allProjects.map((proj,ind)=>
                        <GridListTile>
                            <img src={proj.images[0]}/>

                            <GridListTileBar
                                title={proj.title}
                                subtitle={<span>{proj.categories.join(', ')}</span>}
                                actionIcon={
                                    <IconButton aria-label={`info about ${proj.title}`}>
                                        <InfoIcon />
                                    </IconButton>
                                }
                            />
                        </GridListTile>
                    )}
                </GridList>

            </Container>
        );
    }
}

export default withRouter(Projects);
